import React, { useState, useEffect } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import CssBaseline from '@mui/material/CssBaseline';
import { Box, Container, Tabs, Tab } from '@mui/material';
import { styled } from '@mui/material/styles';

import GoogleContext from '../../context/GoogleContext';

import Header from '../../components/Header';
import Home from '../Home';
import PublicMaps from '../PublicMaps';
import PrivateMaps from '../PrivateMaps';
import Articles from '../Articles';
import AboutUs from '../AboutUs';

import MapDetail from '../PublicMaps/Detail';

const ModeTab = styled(Tab)({
  textTransform: 'none',
  color: '#FFF',
  fontWeight: 200,
  opacity: 0.5,
  '&.Mui-selected': { 
    fontWeight: 600,
    color: '#FFF',
    opacity: 1
  }
})

function App() {

  const [tab, setTab] = useState(0);
  const [google, setGoogle] = useState(null);

  const handleChange = (event, newValue) => {
    setTab(newValue)
  }

  useEffect(() => {
    if (!google) {
      const head = document.head;
      let script = document.getElementById('googleChartsScript');
      if (!script) {
        script = document.createElement('script');
        script.src = 'https://www.gstatic.com/charts/loader.js';
        script.id = 'googleChartsScript';
        script.onload = () => {
          if (window.google && window.google.charts) {
            window.google.charts.load('current', {'packages':['corechart', 'geochart']});
            
            window.google.charts.setOnLoadCallback(() => setGoogle(window.google))
          }
        };
        head.appendChild(script);
      } else if (window.google) {
        setGoogle(window.google);
      }
    }
    return () => {
      let script = document.getElementById('googleChartsScript');
      if (script) {
        script.remove();
      }
    }
  }, [google]);

  return (
    <div className="App">
      <BrowserRouter>
        <GoogleContext.Provider value={{google, setGoogle}}>
          <CssBaseline />
          <Container maxWidth={false} disableGutters>
            <Header />
          </Container>
        <Routes>
          <Route path="/1vyou" element={<Home />} />
          <Route path="/1vyou/public-maps" element={<PublicMaps />} />
          <Route path="/1vyou/public-maps/detail" element={<MapDetail />} />
          <Route path="/1vyou/private-maps" element={<PrivateMaps />} />
          <Route path="/1vyou/articles" element={<Articles />} />
          <Route path="/1vyou/about-us" element={<AboutUs />} />
        </Routes>
        </GoogleContext.Provider>
      </BrowserRouter>
    </div>
  );
}

export default App;
