import React, { useState, useEffect } from "react";
import {
    Container,
    Box,
    Grid,
    Typography,
    Button,
    IconButton,
    Snackbar
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import CloseIcon from '@mui/icons-material/Close';
import { useNavigate, Route } from 'react-router-dom';

import Map from '../../components/Map';
import MapCard from '../../components/MapCard';

import Map1Img from '../../static/map-1.png';
import Map2Img from '../../static/map-2.png';
import Map3Img from '../../static/map-3.png';
import Map4Img from '../../static/map-4.png';
import Map5Img from '../../static/map-5.png';

function PublicMaps() {
  const [videoId, setVideoId] = useState("");
  const [news, setNews] = useState([]);
  const [showSnackbar, setShowSnackbar] = useState(false);

  const navigate = useNavigate();
  
  useEffect(() => {
    
    fetch("https://www.googleapis.com/youtube/v3/search?part=snippet&maxResults=1&q=regulation&type=video&key=AIzaSyDIVRHsNbdQq7H3AF9nRNvuV6VFJJbF6p0")
      .then(res => res.json())
      .then((result) => {
            console.log(result)
            setVideoId(result.items[0].id.videoId);
        },
        (error) => {
            console.log(error);
        }
      )
    
    fetch("https://newsapi.org/v2/everything?q=regulation&from=2022-02-28&sortBy=publishedAt&pageSize=2&apiKey=bcb83d42d34941eb8e8aa654fd98a086")
      .then(res => res.json())
      .then((result) => {
            setNews(result.articles);
        },
        (error) => {
            console.log(error);
        }
      )
  }, []);

  const handleMapClick = (index) => {
    console.log(index);
    if (index == 1) {
        navigate('./detail');
    } else {
        setShowSnackbar(true);
    }
  }

  const handleSnackbarClose = () => {
    setShowSnackbar(false)
  }

  const CloseButton = (
    <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleSnackbarClose}
    >
        <CloseIcon fontSize="small" />
    </IconButton>
    )

//   return (
//     <Container maxWidth={false} sx={{ p: 2 }}>
//       <Grid container spacing={2}>
//           <Grid item xs={12}>
//               <h2 style={{ float: 'left', color: '#3639AE' }}>CRYPTO</h2>
//           </Grid>
//           <Grid item xs={5}>
//               <div>
//                   <iframe 
//                       width="540" 
//                       height="280" 
//                       src={`https://www.youtube.com/embed/${videoId}?autoplay=1&mute=1`}>
//                   </iframe>
//               </div>
//               <div>
//                   {/* {news.map((n) => {
//                       return (
//                           <div style={{ border: "1px solid grey", borderRadius: "10px", margin: "7px 0px" }}>
//                               <a href={n.url} target="_blank">
//                                   <h4>{n.title}</h4>
//                                   <p>{n.description}</p>
//                               </a>
//                           </div>
//                       )
//                   })} */}
//               </div>
//           </Grid>
//           <Grid item xs={7}>
//               <Map />
//           </Grid>
//       </Grid>
//     </Container>
//   )

    return (
        <>
            {/* <Route path="./detail" element={<Map />} /> */}
            <Container maxWidth={false}>
                <Box
                    display="flex"
                    justifyContent="space-between"
                    alignItems="center"
                    mt={4}
                    mb={2}
                    mx={2}
                >
                    <Typography component="h4" sx={{ fontWeight: 600 }}>POLICY MAPS</Typography>
                    <Button variant="contained" startIcon={<AddIcon />} sx={{ backgroundColor: '#3BB77E', px: 4, textTransform: 'none' }}>Create Map</Button>
                </Box>

                <Grid container spacing={2} sx={{ py: 2 }}>
                    <Grid item xs={12} sm={6} md={4} lg={3} onClick={() => handleMapClick(-1)}>
                        <MapCard
                            img={`/1vyou/${Map1Img}`}
                            title="Data Breach"
                            owner="Sammer Fam"
                            region="WorldWide"
                            lastUpdate="4 Weeks ago"
                        />
                    </Grid>

                    <Grid item xs={12} sm={6} md={4} lg={3} onClick={() => handleMapClick(1)}>
                        <MapCard
                            img={`/1vyou/${Map2Img}`}
                            title="Crypto World-wide Regulations"
                            owner="Sammer Fam"
                            region="WorldWide"
                            lastUpdate="4 Weeks ago"
                        />
                    </Grid>

                    <Grid item xs={12} sm={6} md={4} lg={3} onClick={() => handleMapClick(-1)}>
                        <MapCard
                            img={`/1vyou/${Map3Img}`}
                            title="Data Breach Notification Laws"
                            owner="Sammer Fam"
                            region="WorldWide"
                            lastUpdate="4 Weeks ago"
                        />
                    </Grid>

                    <Grid item xs={12} sm={6} md={4} lg={3} onClick={() => handleMapClick(-1)}>
                        <MapCard
                            img={`/1vyou/${Map4Img}`}
                            title="Metaverse"
                            owner="Sammer Fam"
                            region="WorldWide"
                            lastUpdate="4 Weeks ago"
                        />
                    </Grid>

                    <Grid item xs={12} sm={6} md={4} lg={3} onClick={() => handleMapClick(-1)}>
                        <MapCard
                            img={`/1vyou/${Map5Img}`}
                            title="Landlord Tenant Laws"
                            owner="Sammer Fam"
                            region="WorldWide"
                            lastUpdate="4 Weeks ago"
                        />
                    </Grid>
                    
                </Grid>

                <Snackbar
                    open={showSnackbar}
                    autoHideDuration={10000}
                    onClose={handleSnackbarClose}
                    message="Data Extraction is in progress. Try again after sometime."
                    action={CloseButton}
                    anchorOrigin={{ horizontal: 'center', vertical: 'top' }}
                />
            </Container>
        </>
    )

};

export default PublicMaps;