import React, { useState, useEffect } from 'react';
import { Container, Grid, FormControl, InputLabel, Select, MenuItem, Box } from '@mui/material';
import axios from 'axios';
import * as _ from 'lodash';

import Map from '../../../components/Map';

export default function MapDetail() {
    const [sources, setSources] = useState([
        { label: 'Regulation', value: 'regulations', source: '', data: [] },
        { label: 'Tax Laws', value: 'tax_laws', source: '', data: [] },
        { label: 'ICOs', value: 'icos', source: '', data: [] },
        { label: 'Exchanges', value: 'exchanges', source: '', data: [] },
        { label: 'CBDC (Central Bank Digital Currency)', value: 'cdbc', source: '', data: [] },
    ]);
    const [activeSource, setActiveSource] = useState(null);
    const [videoId, setVideoId] = useState("");


    useEffect(() => {
        axios.get('https://xg2b2kjxah.execute-api.us-east-2.amazonaws.com/extracted/extract_data').then(response => {
            if (response.status == 200 && response.data) {
                let data = response.data;
                let sourcesCopy = [...sources];
                sources.forEach((source, index) => {    
                    if (data[source.value]) {
                        sourcesCopy[index] = {
                            ...source,
                            data: [['Country', source.label],...data[source.value].map(val => ([val.Country, val.Status]))]
                        }
                        let uniqueValues = _.uniq(sourcesCopy[index].data.slice(1).map(d => d[1]))
                        console.log('uniqueValues', uniqueValues);
                        uniqueValues = uniqueValues.reduce((acc, val, index) => {
                            acc[val] = index
                            return acc
                        }, {});
                        sourcesCopy[index].data = sourcesCopy[index].data.map((d , i) => {
                            if (i > 0) {
                                d[2] = uniqueValues[d[1]]
                            }
                            return d
                        })

                        console.log(sourcesCopy[index].data)
                    }
                })
                console.log('sourcesCopy', sourcesCopy)
                setSources(sourcesCopy);
            }  
        })
    
        // axios.get("https://newsapi.org/v2/everything?q=regulation&from=2022-02-28&sortBy=publishedAt&pageSize=2&apiKey=bcb83d42d34941eb8e8aa654fd98a086")
        // .then(res => console.log(res))
    }, []);

    useEffect(() => {
        if (activeSource) {
            axios.get(`https://www.googleapis.com/youtube/v3/search?part=snippet&maxResults=1&q=${activeSource.label}&type=video&key=AIzaSyDelCkFXfcsSB3RN47H2j_2kXc_YqX8NeE`)
                .then(res => {
                    if (res.status == 200 && res.data) {
                        setVideoId(res.data.items[0].id.videoId);
                    }
                })
        }
    }, [activeSource])

    const handleDataSourceChange = (event) => {
        setActiveSource(event.target.value)
    }

    console.log(sources)
    
    return (
        <Container maxWidth={false} sx={{ py: 2, my: 2 }}>
            <Grid container spacing={2}>
                <Grid item sm={12} md={4} lg={5}>
                    <div style={{ textAlign: 'center' }}>
                        <iframe 
                            width="540" 
                            height="280" 
                            src={`https://www.youtube.com/embed/${videoId}?autoplay=1&mute=1`}
                        >
                      </iframe>
                    </div>
                </Grid>
                <Grid item sm={12} md={8} lg={7}>
                    <Box my={2}>
                        <FormControl fullWidth>
                            <InputLabel id="data-source-select-label">Data Source</InputLabel>
                            <Select
                                labelId="data-source-select-label"
                                id="data-source-select"
                                value={activeSource}
                                label="Data Source"
                                onChange={handleDataSourceChange}
                            >
                                {
                                    sources.map(source => (
                                        <MenuItem value={source}>{ source.label }</MenuItem>
                                    ))
                                }
                            </Select>
                        </FormControl>
                    </Box>
                    <Map data={activeSource ? activeSource.data : null} />
                </Grid>
            </Grid>
        </Container>
    )
}