import React from 'react';
import {
    Link,
    useLocation
  } from 'react-router-dom';
import {
    Box,
    AppBar,
    Toolbar,
    Typography,
    Container,
    Button,
} from '@mui/material';

import Logo from '../../static/logo-1vyou.png';

export default function Header() {

    const location = useLocation();

    return (
        <Box sx={{ flexGrow: 1 }}>
            <AppBar
                position="static"
                sx={{ backgroundColor: '#FFFFFF' }}
            >
                <Container maxWidth={false}>
                    <Toolbar
                        disableGutters
                        sx={{ backgroundColor: '#FFFFFF', justifyContent: 'space-between', alignItems: 'flex-end' }}
                    >
                        <Box display="flex" alignItems="center" py={1}>
                            <a href="/1vyou">
                                <img src={`/1vyou/${Logo}`} alt="1vyou" width="48" height="45" style={{ cursor: 'pointer'}} />
                            </a>
                            <Typography
                                variant="h6"
                                noWrap
                                component="div"
                                sx={{ ml: 2, color: '#000' }}
                            >
                                1vYou
                            </Typography>
                        </Box>

                        <Box>
                            <Link to="/1vyou" style={{ textDecoration: 'none' }}>
                                <Button sx={{
                                    py: 2,
                                    mx: 2,
                                    fontSize: '15px',
                                    color: '#000',
                                    fontWeight: 600,
                                    textTransform: 'none',
                                    borderRadius: 0,
                                    borderBottom: location.pathname == "/1vyou" ? '3px solid #000' : 'none',
                                    color: location.pathname == "/1vyou" ? '#36B37E' : '#000',
                                    }}
                                >
                                    Home
                                </Button>
                            </Link>
                            <Button sx={{
                                py: 2,
                                mx: 2,
                                fontSize: '15px',
                                color: '#000',
                                fontWeight: 600,
                                textTransform: 'none',
                                borderRadius: 0,
                                borderBottom: location.pathname.startsWith("/1vyou/public-maps") ? '3px solid #000' : 'none'
                                }}>
                                <Link to="/1vyou/public-maps" style={{ color: location.pathname.startsWith("/1vyou/public-maps") ? '#36B37E' : '#000', textDecoration: 'none' }}>Public Maps</Link>
                            </Button>
                            <Button sx={{
                                py: 2,
                                mx: 2,
                                fontSize: '15px',
                                color: '#000',
                                fontWeight: 600,
                                textTransform: 'none',
                                borderRadius: 0,
                                borderBottom: location.pathname == "/1vyou/private-maps" ? '3px solid #000' : 'none'
                                }}>
                                <Link to="/1vyou/private-maps" style={{ color: location.pathname == "/1vyou/private-maps" ? '#36B37E' : '#000', textDecoration: 'none' }}>Private Maps</Link>
                            </Button>
                            <Button sx={{
                                py: 2,
                                mx: 2,
                                fontSize: '15px',
                                color: '#000',
                                fontWeight: 600,
                                textTransform: 'none',
                                borderRadius: 0,
                                borderBottom: location.pathname == "/1vyou/articles" ? '3px solid #000' : 'none'
                                }}>
                                <Link to="/1vyou/articles" style={{ color: location.pathname == "/1vyou/articles" ? '#36B37E' : '#000', textDecoration: 'none' }}>Articles</Link>
                            </Button>
                            <Button sx={{
                                py: 2,
                                mx: 2,
                                fontSize: '15px',
                                color: '#000',
                                fontWeight: 600,
                                textTransform: 'none',
                                borderRadius: 0,
                                borderBottom: location.pathname == "/1vyou/about-us" ? '3px solid #000' : 'none'
                                }}>
                                <Link to="/1vyou/about-us" style={{ color: location.pathname == "/1vyou/about-us" ? '#36B37E' : '#000', textDecoration: 'none' }}>About Us</Link>
                            </Button>
                        </Box>
                    </Toolbar>
                </Container>
            </AppBar>
        </Box>
    )
}