import React from 'react';
import { Card, CardMedia, CardContent, CardActions, Typography, Button } from '@mui/material';

export default function MapCard({
    img,
    title,
    owner,
    region,
    lastUpdate
}) {
    return (
        <Card sx={{ maxWidth: 320, borderRadius: '26px', margin: 'auto', cursor: 'pointer', '&:hover': {
            transform: 'scale(1.1)'
        }}}>
            <CardMedia
                component="img"
                height="260"
                image={img}
            />
            <CardContent sx={{ textAlign: 'center' }}>
                <Typography gutterBottom variant="h5" component="div" sx={{ textAlign: 'center', fontWeight: 700 }}>{ title }</Typography>
                <div style={{ display: 'flex', justifyContent: 'center' }}>
                    <Typography variant="body2" color="text.secondary" sx={{ fontWeight: 600, mr: 1 }}>Owner:</Typography>
                    <Typography variant="body2" color="text.secondary" sx={{ mr: 1 }}>{ owner }</Typography>
                    <Typography variant="body2" color="text.secondary" sx={{ fontWeight: 600, mr: 1 }}>Region:</Typography>
                    <Typography variant="body2" color="text.secondary" sx={{ mr: 1 }}>{ region }</Typography>
                </div>
                <div style={{ display: 'flex', justifyContent: 'center' }}>
                    <Typography variant="body2" color="text.secondary" sx={{ fontWeight: 600, mr: 1 }}>Last Update:</Typography>
                    <Typography variant="body2" color="text.secondary" sx={{  }}>{ lastUpdate }</Typography>
                </div>
            </CardContent>
        </Card>
    )
}