import React, { useState, useEffect, useContext } from 'react';
import { CircularProgress } from '@mui/material';
import { Chart } from "react-google-charts";

import GoogleContext from '../../context/GoogleContext';

export default function Map({
    data
}) {

    const [chart, setChart] = useState(null);
    const { google } = useContext(GoogleContext);

    useEffect(() => {
        if (google && data) {
            let formattedData = [...data];
            let mapData = new window.google.visualization.DataTable();
            mapData.addRows(formattedData.length - 1);
            mapData.addColumn('string', formattedData[0])
            mapData.addColumn('number', formattedData[1])

            formattedData.slice(1).forEach((row, index) => {
                mapData.setValue(index, 0, row[0])
                mapData.setValue(index, 1, row[2])
                mapData.setFormattedValue(index, 1, row[1])
            })
            
        
            let options = {
                'width': '100%',
                'height': 450,
                colorAxis: {colors: ['#58BE34', '#A2A4ED']},   
                datalessRegionColor: '#f5f5f5',
                defaultColor: '#f5f5f5'
            };

            if (!chart) {
                // Instantiate and draw our chart, passing in some options.
                const newChart = new window.google.visualization.GeoChart(document.getElementById('geoChart'));
                console.log(mapData)
                newChart.draw(mapData, options);
                setChart(newChart);
            } else {
                console.log(mapData)
                chart.draw(mapData, options);
            }
        }
      }, [google, chart, data]);

    return (
        <>
            {!google && <CircularProgress />}
            <div id="geoChart" className={!google ? 'd-none' : ''} />
        </>
    )
}